import { CustomStore } from './CustomStore';
class CustomStagesStore extends CustomStore {
    addStages(stages) {
        this.set(stages);
    }
    addStage(stage) {
        this.update((stages) => stages.find((stg) => stg.id === stage.id)
            ? [...stages]
            : [...stages, stage]);
    }
    removeStage(stage) {
        this.update((stages) => stages.filter((stg) => stg.id !== stage.id));
    }
    updateStage(stage) {
        this.update((stages) => stages.map((stg) => stg.id === stage.id
            ? stage
            : stg));
    }
    removeStagesOfCategory(categoryId) {
        this.update((stages) => stages.filter((stg) => stg.categoryId !== categoryId));
    }
}
export const customStagesStore = new CustomStagesStore([]);
