import { CustomStore } from './CustomStore';
class CustomCategoriesStore extends CustomStore {
    addCategories(categories) {
        this.set(categories);
    }
    addCategory(category) {
        this.update((categories) => categories.find((cat) => cat.id === category.id)
            ? [...categories]
            : [...categories, category]);
    }
    removeCategory(category) {
        this.update((categories) => categories.filter((cat) => cat.id !== category.id));
    }
    updateCategory(category) {
        this.update((categories) => categories.map((cat) => cat.id === category.id
            ? category
            : cat));
    }
}
export const customCategoriesStore = new CustomCategoriesStore([]);
